.post{
    width: 385px;
    margin: 0px 25px 40px 25px;
}

.postImg {
width: 100%;
height: 280px;
object-fit: cover;
border-radius: 7px;
}

.postInfo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.postCatgs {
    font-family: 'Times New Roman', Times, serif;
font-size: 11px;
color: #be9676;
line-height: 20px;
margin-top: 15px;
margin-right: 10px;
cursor: pointer;
}

.postTitle {
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 24px;
    font-weight: 700;
    margin-top: 15px;
    cursor: pointer;
}

.postDate {
    font-family: 'Lora', serif;
    font-style: italic;
    font-size: 13px;
    color: #999;
    margin-top: 15px;
}

.postDest {
    font-family:Georgia, 'Times New Roman', Times, serif ;
font-size: 14px;
color: #444;
line-height: 24px;
margin-top: 15px;
overflow: hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
}